.app-hero {
  position: relative;
  padding-bottom: 250px;
  .patttern-one {
    position: absolute;
    left: -100px;
    display: none;

    @include for-normal-layout {
      display: block;
    }
  }
  .content {
    flex-direction: column;
    @include for-normal-layout {
      flex-direction: initial;
    }
    .patttern-two {
      position: absolute;
      right: unset;
      bottom: -6px;
      @include for-normal-layout {
        position: absolute;
        right: 200px;
        bottom: -6px;
      }

      
    }
    h1 {
      color: #ffffff;
      font-size: 40px;
      font-weight: 700;
      line-height: 40px;
      text-align: center;
      margin-bottom: 20px;
      @include for-normal-layout {
        margin-bottom: 0px;
        text-align: initial;
        font-size: 100px;
        font-weight: 700;
        line-height: 100px;
      }
      span {
        color: #f67e7e;
      }
    }
    p {
      color: #ffffff;
      font-size: 15px;
      font-weight: 600;
      line-height: 28px;
      text-align: center;
      br {
        display: none;
      }

      @include for-normal-layout {
        text-align: initial;
        font-size: 18px;
        font-weight: 600;
        line-height: 28px;
        br {
          display: block;
        }
      }
    }
    .pattern {
      width: 50px;
      height: 4px;
      background-color: #79c8c7;
      display: none;
      @include for-normal-layout {
        display: block;
      }
    }
  }
}
