.app-footer {
  background-color: #002529;
  height: unset;
  @include for-normal-layout {
    height: 200px;
  }

  .content {
    height: 100%;
    align-items: center;
    flex-direction: column;
    padding-bottom: 64px;
    @include for-normal-layout {
      padding-bottom: 0px;
      flex-direction: row;
    }



    .logo {
      margin-bottom: 25px;
      margin-top: 66px;
      @include for-normal-layout {

        margin-bottom: 25px;
        margin-top: 0px;
      }

    }

    p {
      color: #ffffff;
      font-size: 18px;
      font-weight: 600;
      line-height: 28px;
      margin-bottom: 24px;
      

      @include for-normal-layout {
        margin-bottom: 0px;
        color: #ffffff;
        font-size: 18px;
        font-weight: 600;
        line-height: 28px;
      }
    }

    .box-one {
      width: unset;
      color: #ffffff;
      font-family: levic-semi-bold;
      font-size: 15px;
      font-weight: 600;
      line-height: 25px;
      opacity: 0.6;
      text-align: center;
      margin-bottom: 46px;
      @include for-normal-layout {
        text-align: unset;
        margin-bottom: 0px;


        width: 445px;
      }
    }


    .box-two {
      .box-two-content {
        margin-bottom: 16px;
        justify-content: center;
        @include for-normal-layout {
          justify-content: unset;
          margin-bottom: 50px;
        }

        
        .social:not(:first-child) {
          margin-right: 16px;
        }
      }
      p {
        opacity: 0.6;
        color: #ffffff;
        font-size: 15px;
        font-weight: 600;
        line-height: 25px;
        margin-bottom: 0px;


      }
    }
  }
}
