.app-creators {
  background-color: #004047;
  position: relative;
  .pattern-one {
    position: absolute;
    left: -10px;
  }
  .pattern-two {
    position: absolute;
    bottom: -6px;
    right: 0px;
  }
  .content {
    padding-top: 140px;
    
    h1 {
      color: #ffffff;
      font-size: 32px;
      font-weight: 700;
      line-height: 32px;
      text-align: center;
      margin-bottom: 86px;
      br {
        display: none;
      }
      span {
        color: #79c8c7;
      }

      @include for-normal-layout {
        font-size: 48px;
        font-weight: 700;
        line-height: 48px;
        text-align: center;
        margin-bottom: 95px;
        br {
          display: block;
        }
      }
    }
    .box-holder {

      padding-bottom: 164px;
      display: block;
      @include for-normal-layout {
        padding-bottom: 140px;
        display: flex;
      }
      .box {
        // margin-bottom: 84px;
        // @include for-normal-layout {
        //   margin-bottom: 0px;
        // }


        .txt-holder {
          position: relative;
          .pattern-three {
            position: absolute;
            right: 42%;
            top: -33px;

            @include for-normal-layout {
              position: absolute;
              right: 138px;
              top: -33px;
            }
          }

          .txt {
            position: relative;
            p {
              text-align: center;
              color: #ffffff;
              font-size: 15px;
              font-weight: 600;
              line-height: 25px;
              margin-bottom: 27px;
              br{
                display: none;
              }
              @include for-normal-layout {
                br{
                  display: block;
                }
              }
            }
          }
        }
        h1 {
          text-align: center;
          color: #79c8c7;
          font-size: 18px;
          font-weight: 700;
          line-height: 28px;
          margin-bottom: 0px;
        }
        h2 {
          text-align: center;

          color: #ffffff;
          font-size: 13px;
          font-weight: 500;
          line-height: 18px;
          margin-bottom: 32px;
        }
        .img-holder {
          img {
            border-radius: 50px;
            border: 2px solid #c4fffe;
            width: 62px;
            height: 62px;
          }
        }
      }
      .box:not(:last-child){
        margin-bottom: 84px;
        @include for-normal-layout {
          margin-bottom: 0px;
        }


      }
    }

    // .box-holder:not(:last-child){
    //   padding-bottom: 140px;



    // }
  }
}
