.app-get-started {
  height: 302px;
  background-color: #f67e7e;
  position: relative;
  @include for-normal-layout {
    height: 200px;
  }

  .pattern {
    position: absolute;
    bottom: -6px;
  }
  .content {
    height: 100%;
    flex-direction: column;
    justify-content: center;
    @include for-normal-layout {
      flex-direction: row;
      justify-content: space-between;
    }

    h1 {
      color: #012f34;
      font-size: 32px;
      font-weight: 700;
      line-height: 32px;
      margin-bottom: 54px;
      @include for-normal-layout {
        font-size: 48px;
        font-weight: 700;
        line-height: 48px;
        margin-bottom: 0px;
      }

    }
  }
}
