.app-about {
  background-color: #012f34;
  .pattern-one {
    position: absolute;
    right: -127px;
    bottom: unset;
    display: none;
    @include for-normal-layout {
      display: block;
      position: absolute;
      right: -127px;
      bottom: -547px;
    }
  }
  .content {
    padding-top: 64px;
    padding-bottom: 92px;

    @include for-normal-layout {
      padding-top: 198px;
      padding-bottom: 140px;
    }

    .pattern {
      width: 50px;
      height: 4px;
      background-color: #f67e7e;
      margin-bottom: 54px;
    }
    .content-holder {
      display: unset;
      @include for-normal-layout {
        display: flex;
      }

      h1 {
        color: #ffffff;
        font-size: 32px;
        font-weight: 700;
        line-height: 32px;
        margin-bottom: 90px;

        @include for-normal-layout {
          margin-bottom: 0px;
          font-size: 48px;
          font-weight: 700;
          line-height: 48px;
        }
      }
      .box-holder {
        .box {
          margin-bottom: 48px;
          display: block;

          @include for-normal-layout {
            margin-bottom: 32px;
            display: flex;
          }
          .logo {
            width: unset;
            margin-right: 0px;
            display: flex;
            justify-content: center;
            @include for-normal-layout {
              width: 65px;
              display: unset;
              margin-right: 23px;
            }
          }
          h1 {
            color: #f67e7e;
            font-size: 18px;
            font-weight: 700;
            line-height: 28px;
            margin-bottom: 8px;
            text-align: center;
            @include for-normal-layout {
              text-align: unset;
              font-size: 18px;
              font-weight: 700;
              line-height: 28px;
              margin-bottom: 16px;
            }
          }
          p {
            color: #ffffff;
            font-size: 15px;
            font-weight: 600;
            line-height: 25px;
            opacity: 0.8;
            text-align: center;
            br {
              display: none;
            }

            @include for-normal-layout {
              text-align: unset;
              color: #ffffff;
              font-size: 15px;
              font-weight: 600;
              line-height: 25px;
              opacity: 0.8;
              br {
                display: block;
              }
            }
          }
        }
      }
    }
  }
}
