.appHeader-nav-color {
  // background-color: rgb(200, 228, 228);
  background-color: #014e56;
  .appHeader-nav {
    margin-top: 73px;
    margin-bottom: 133px;
    font-family: levic-semi-bold;

    .logo {
      margin-right: 80px;
    }

    .appHeader-nav-links {
      align-items: center;
      justify-content: center;
      display: none;
      @include for-narrow-layout {
        display: none;
      }
      @include for-normal-layout {
        display: flex;
      }

      p {
        color: #ffffff;
        font-size: 18px;
        font-weight: 600;
        line-height: 28px;
        margin-right: 43px;
      }
    }
    .btn-holder {
      display: none;

      @include for-narrow-layout {
        display: none;
      }
      @include for-normal-layout {
        display: block;
      }
    }

    .borger-btn-holder {
      @include for-narrow-layout {
        display: block;
      }
      @include for-normal-layout {
        display: none;
      }
      .appHeader-menu-btn {
        width: auto;

        padding: 0px;
        border: none;
        background-color: unset;
        font-size: 24px;
        outline: none;
        color: white;
      }
    }
  }
}

