* {
  box-sizing: border-box;
}

::selection {
  background-color: $clr1;
  color: $clr6;
}

html {
  overflow-x: hidden;

  line-height: 1.6;
  scroll-behavior: smooth;
  background-color: #014e56;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

h1 {
  font-family: levic-bold;
}
h2 {
  font-family: levic-medium-italic;
}

p {
  margin: 0;
  font-family: levic-semi-bold;
}

li {
  list-style: none;
}
